	module.exports = {"data":{"post":{"html":"<p>This morning is the big meeting with the bank. In the middle of preparing her presentation, MamaMaus came storming into the living room. \"I am an eagle!\" she cried, which confused Christopher to no end. But if it helps to convince the Deutsche Bank, she can be whatever she wants.</p>\n<p>Eagles work alone, so the vulture will stay home at the nest and cross his fingers.</p>","fields":{"title":"I Am An Eagle!","date":"22 May 2007","url":"https://papascott.de/2007-05-22-i-am-an-eagle/"}}},"pathContext":{"slug":"/2007-05-22-i-am-an-eagle/","prev":{"fields":{"title":"For Want of a Nail","date":"22 May 2007","slug":"/2007-05-22-for-want-of-a-nail/"}},"next":{"fields":{"title":"SethBlog in Europe","date":"22 May 2007","slug":"/2007-05-22-sethblog-in-europe/"}}}}

